import { createAppSlice } from "@/lib/app.slice";
import type { AppThunk } from "@/lib/store";
import type { PayloadAction } from "@reduxjs/toolkit";

import DataInvoice from "@/lib/data/documents.json"

const dataInvoice = DataInvoice;

export interface DocumentOCRState {
  file: any;
  filename: "";
  data: any;
}

const initialState: DocumentOCRState = {
  file: null,
  filename: "",
  data: null,
};

// If you are not using async thunks you can use the standalone `createSlice`.
export const documentSlice = createAppSlice({
  name: "document",
  // `createSlice` will infer the state type from the `initialState` argument
  initialState,
  // The `reducers` field lets us define reducers and generate associated actions
  reducers: (create) => ({
    upload: (state, action: PayloadAction< { file: any; filename: "" } >) => {
      // Redux Toolkit allows us to write "mutating" logic in reducers. It
      // doesn't actually mutate the state because it uses the Immer library,
      // which detects changes to a "draft state" and produces a brand new
      // immutable state based off those changes

      const { file, filename } = action.payload;
      state.file = file;
      state.filename = filename;

      state.data = dataInvoice.find(item => {
        return item.filename == filename
       });
      
    },
  }),
  // You can define your selectors here. These selectors receive the slice
  // state as their first argument.
  selectors: {
    selectFile: (document) => document.file,
    selectFileName: (document) => document.filename,
    selectData: (document) => document.data,
  },
});

// export const upload = createAction< { file: any; filename: string; status: string } >('upload');

// Action creators are generated for each case reducer function.
export const { upload } = documentSlice.actions;

// Selectors returned by `slice.selectors` take the root state as their first argument.
export const { selectFile, selectFileName, selectData } = documentSlice.selectors;